bargo, la figura de representación del grupo se encuentra en el tío paterno. Por otro lado, como lo es en el caso de los nukak esta filiación se hereda de manera patrilineal; es decir, por vía de consanguinidad paterna. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Dentro de los nukak la jerarquía y el rol social a desempeñar dependen del género y la edad y así mismo de la cantidad de trabajo realizado. Esto influye en distintas áreas de la vida como lo es la alimentación. En este caso los niños huérfanos y que no tienen pertenencia a un grupo o clan determinado serán los últimos en recibir alimento. De este modo es posible que muchas costumbres choquen con las creencias y conceptos que maneja la sociedad occidental respecto a la infancia. En este contexto podrían pensarse otras costumbres que hacen parte de las pautas de crianza como lo es la ablación del clítoris en algunas comunidades emberá o la reclusión en cuarto por varios días tras la primera menstruación en la comunidad wayuu. ")])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img4.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_1_2","data-aos":"zoom-in-left"}},[_c('h2',[_vm._v("1.2. Lenguas propias ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"cajon-b color-primario p-3"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("En Colombia existen aproximadamente 65 lenguas indígenas que se encuen