on las autoridades políticas, son designados por las autoridades tradicionales quienes ejercen sobre ellos un control, en otras comunidades, las autoridades tradicionales hacen parte de la conformación de los cabildos. En algunas comunidades, como los wayuu, en donde existen códigos sancionatorios y compensaciones y existen especialistas en derecho que median los conflictos sociales. Los sistemas de autoridad son diferentes en las distintas comunidades, en algunas es de carácter familiar, en otras, como los nómadas de la selva conformados por clanes, la autoridad en la unidad familiar la ejerce el hombre, en otros pueblos la máxima autoridad es religiosa, en donde los cabildantes y alguaciles se encuentran subordinados a esta (Sierra Nevada y los u’wa). ")]),_c('div',{staticClass:"bloque-texto-b color-secundario p-4"},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('i',{staticClass:"fas fa-quote-left"}),_c('h5',[_vm._v("En los pueblos amazónicos el dueño de maloka –payé, taita– y los viejos –sabedores– a través del conocimiento y manejo de la tradición y los rituales, ejercen el control y manejo de la vida social. El capitán –la autoridad civil– se encarga de las relaciones con las instituciones. Pueblos como el tule realizan frecuentes asambleas donde delibera toda la comunidad bajo la autoridad de un saila."),_c('br')]),_c('p',{staticClass:"mb-0"},[_vm._v("(Departamento Nacional de Planeación, DNP, 2004).")]),_c('i',{staticClass:"fas fa-quote-right"})])]),_c('p',{staticClass:"mt-3",attrs:{"data-aos":"fade-down"}},[_vm._v("A partir de la década de 1970 comienzan a conformarse organizaciones indígenas de reivindicación de carácter regional y de carácter nacional, que asumen la vocería y negociación con las instituciones del Estado.")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"img-a img-t",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img20.svg"),"alt":""}})])]),_c('div',{staticClass:"row justify-content-center align-items-cen