erritorio de los hermanos menores; es decir, los que no son de la Sierra. Cada piedra, fuente de agua o cerro tiene un nombre y una función de la que dan cuenta las historias tradicionales que narra el mamö o mama, su dirigente espiritual. El corazón del mundo corresponde a los picos nevados, hogar de Serankua, el creador. Cuando se cruza una montaña se ofrece un pagamento a los espíritus que moran allí. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5 cajon color-acento-contenido",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-0 ps-3",attrs:{"data-aos":"fade-down"}},[_vm._v("Los wayuu de La Guajira delimitan y reafirman sus territorios claniles mediante sus cementerios. Algunos pueblos como el uitoto, surgió de la tierra a través de unos agujeros que están en el Igara -Paraná y para la mayoría de los pueblos amazónicos los ríos recuerdan la Anaconda Ancestral de cuyos fragmentos nacieron los pueblos y ocuparon un lugar jerárquico en los ríos. Las culturas amerindias no solamente consideran la tierra como la madre, el territorio tradicionalmente ocupado constituye la base para su reproducción biológica y cultural (Departamento Nacional de Planeación, DNP, 2004). Para los pueblos indígenas el territorio es comunitario e históricamente ha habido un rechazo a la dotación de tierras por parte de los gobiernos en forma de propiedad individual. ")])]),_c('div',{staticClass:"col-lg-5 p-0"},[_c('img',{attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img26.jpg"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_1_7","data-aos":"zoom-in-left"}},[_c('h2',[_vm._v("1.7. Colonización ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div