a colombianas. Posteriormente, la explotación de los recursos naturales, minerales e hidrocarburos, la explotación maderera y, más recientemente, el narcotráfico continuaron con el daño a los ecosistemas de los territorios de las comunidades indígenas, y continuo el despojo de sus tierras con la ampliación de la frontera agrícola y con nuevos proyectos de infraestructura.")]),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("En este caso es posible hacer referencia a uno de los procesos de colonización más recientes del territorio colombiano, el cual corresponde al proceso de colonización de las tierras amazónicas, que comenzó a darse desde los Llanos, hacia San José del Guaviare, y otros departamentos como Nariño y Putumayo. La población del Guaviare por parte de los campesinos, se da por dos vías: el río Guayabero y el Ariari, que confluyen en el río Guaviare, por una parte, llegan los colonos armados, y por otra el colono independiente, ambos buscando en estas zonas huir de la violencia y prosperar económicamente, la línea que dividiría a estos dos tipos de colonos se hace cada vez más invisible, ya que la cooperación para el provecho de la tierra se hace necesario, a lo cual lo sigue la organización de estos en sindicatos y en 1985 ocurre la primera toma a San José por parte del sindicato de pequeños agricultores. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Además, el río Guaviare se consolida como una de las vías de comercio principales no únicamente desde la llegada de los colonos a este terreno, ya que los grupos nativos de la zona también utilizaban al río como medio de comunicación con lugares cercanos; sin embargo, resulta clave para el transporte de cultivos como el maíz y el cacao, y más tarde de la coca, producto que 