 pueblos indígenas. El Caribe y el centro de los Andes fueron las primeras regiones que sucumbieron al proceso colonizador, luego vendría la ocupación de las tierras bajas asociada a la apertura de caminos y vías de comunicación y a la explotación de recursos naturales. La Amazonía permaneció relativamente marginada de la vida del resto de la Nación hasta la primera mitad del siglo XX. En la actualidad los resguardos indígenas son un freno a los procesos de colonización. No obstante, persiste la ocupación forzosa de territorios indígenas para la instalación de cultivos ilícitos, la explotación de recursos del bosque y la minería ilegal. "),_c('br')]),_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("(Departamento Nacional de Planeación, DNP, 2004).")])]),_c('i',{staticClass:"color-green fas fa-quote-right"})])])])])}]

